import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Tma009Component } from './tma009/tma009.component';
import { Tma010Component } from './tma010/tma010.component';
import { Tma011Component } from './tma011/tma011.component';

const routes: Routes = [
  {
    path: 'TMA009',
    component: Tma009Component
  },
  {
    path: 'TMA010',
    component: Tma010Component
  },
  {
    path: 'TMA011',
    component: Tma011Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthorizationRoutingModule { }
