import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Tma002Component } from './tma002/tma002.component';
import { Tma003Component } from './tma003/tma003.component';
import { Tma004Component } from './tma004/tma004.component';

const routes: Routes = [
  {
    path: 'TMA002',
    component: Tma002Component
  },
  {
    path: 'TMA003',
    component: Tma003Component
  },
  {
    path: 'TMA004',
    component: Tma004Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VirtualKeyRoutingModule { }
