import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Tma008Component } from './tma008/tma008.component';
import { Tma007Component } from './tma007/tma007.component';
import { Tma005Component } from './tma005/tma005.component';

const routes: Routes = [
  {
    path: 'TMA005',
    component: Tma005Component
  },
  {
    path: 'TMA007',
    component: Tma007Component
  },
  {
    path: 'TMA008',
    component: Tma008Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PreanalisisRoutingModule { }
