import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Tma012Component } from './tma012/tma012.component';
import { Tma013Component } from './tma013/tma013.component';

const routes: Routes = [
  {
    path: 'TMA012',
    component: Tma012Component
  },
  {
    path: 'TMA013',
    component: Tma013Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NoclientsRoutingModule { }
