import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthModule } from './auth/auth.module';
import { PreanalisisModule } from './preanalisis/preanalisis.module';
import { AuthorizationModule } from './authorization/authorization.module';
import { VirtualKeyModule } from './virtual-key/virtual-key.module';
import { NoclientsModule } from './noclients/noclients.module';
import { VinculationModule } from './vinculation/vinculation.module';
import { ForbiddenComponent } from './forbidden/forbidden.component';

const routes: Routes = [
  {
    path: '',
    component: ForbiddenComponent
  },
  {
    path: 'auth',
    loadChildren:  () => AuthModule
  },
  {
    path: 'preanalisis',
    loadChildren: () => PreanalisisModule
  },
  {
    path: 'authorization',
    loadChildren: () => AuthorizationModule
  },
  {
    path: 'virtualKey',
    loadChildren: () => VirtualKeyModule
  },
  {
    path: 'noclients',
    loadChildren: () => NoclientsModule
  },
  {
    path: 'vinculation',
    loadChildren: () => VinculationModule
  },
  {
    path: 'forbidden',
    component: ForbiddenComponent
  },
  {
    path: '**',
    redirectTo: 'forbidden'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TmaRoutingModule { }
