import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Vin001Component } from './vin001/vin001.component';
import { Vin002Component } from './vin002/vin002.component';
import { Vin003Component } from './vin003/vin003.component';
import { Vin004Component } from './vin004/vin004.component';
import { Vin005Component } from './vin005/vin005.component';
import { Vin007Component } from './vin007/vin007.component';
import { Vin006Component } from './vin006/vin006.component';
import { Vin008Component } from './vin008/vin008.component';
import { Vin009Component } from './vin009/vin009.component';

const routes: Routes = [
  {
    path: 'VIN001',
    component: Vin001Component
  },
  {
    path: 'VIN002',
    component: Vin002Component
  },
  {
    path: 'VIN003',
    component: Vin003Component
  },
  {
    path: 'VIN004',
    component: Vin004Component
  },
  {
    path: 'VIN005',
    component: Vin005Component
  },
  {
    path: 'VIN006',
    component: Vin006Component
  },
  {
    path: 'VIN007',
    component: Vin007Component
  },
  {
    path: 'VIN008',
    component: Vin008Component
  },
  {
    path: 'VIN009',
    component: Vin009Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VinculationRoutingModule { }
