import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Tma001Component } from './tma001/tma001.component';
import { Tma014Component } from './tma014/tma014.component';
import { Tma015Component } from './tma015/tma015.component';

const routes: Routes = [
  {
    path: 'TMA001',
    component: Tma001Component
  },
  {
    path: 'TMA014',
    component: Tma014Component
  },
  {
    path: 'TMA015',
    component: Tma015Component
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule { }
